import React from "react";
import classes from "./Second.module.css";
import Heading from "../../component/UI/Heading/Heading";
import List from "../../component/List/List";
import Navigation from "../../component/Navigation/Navigation";
import Bars from "../../component/Bars/Bars";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";

const Second = (props) => {
  return (
    <div className={classes.container}>
      <Bars step={2} />
      <Heading title="Select Speciality" />
      <div className={classes.listContainer}>
        <div className={classes.list}>
          <List title="General Medicine" url="./assests/booking.png"/>
          <List title="Heart" url="./assests/booking.png"/>
          <List title="Kidney & Urinary Issues" url="./assests/booking.png"/>
          <List title="Child Specialist" url="./assests/booking.png"/>
          <List title="General Surgery" url="./assests/booking.png"/>
          <List title="Eye Specialist" url="./assests/booking.png"/>
        </div>
        <div className={classes.list}>
          <List title="Women's Health" url="./assests/booking.png"/>
          <List title="Skin & Hair" url="./assests/booking.png"/>
          <List title="Bone & Joints" url="./assests/booking.png"/>
          <List title="Ayurveda" url="./assests/booking.png"/>
          <List title="Sex Specialist" url="./assests/booking.png"/>
          <List title="Dental Care" url="./assests/booking.png"/>
        </div>
      </div>
      <Navigation
        left={<ArrowBackIcon style={{ fontSize: "30px" }} />}
        right={<ArrowForwardIcon style={{ fontSize: "30px" }} />}
        leftClick={()=> props.history.replace('/first')}
        rightClick={()=> props.history.push('/third')}
      />
    </div>
  );
};

export default Second;
