import React from "react";
import classes from "./fourth.module.css";
import Heading from "../../component/UI/Heading/Heading";
import List from "../../component/List/List";
import Navigation from "../../component/Navigation/Navigation";
import Bars from "../../component/Bars/Bars";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import Details from "../../component/Details/Details";
import DateDisplay from "../../component/DateDisplay/DateDisplay";
import SubHeading from "../../component/UI/SubHeading/SubHeading";

const Fourth = (props) => {
  return (
    <div className={classes.container}>
      <Bars step={4} />
      <Heading title="Select a time slot" />
      <Details
        url="./assests/booking.png"
        name="Dr.Daisy Murphy"
        speciality="Dermatologist"
        exp={11}
        address="15780 Mckee Rd, upatoi, GA, 31829"
      />

      <DateDisplay left title="Mon, 25Feb" date right />
      <div className={classes.listContainer}>
      <SubHeading title="Pick Time" subTitle="(6 slots available)" />
        <div className={classes.timeInterval}>
          <List title="Morning" url="./assests/medocal icons/morning.png" />
          <List title="Afternoon" url="./assests/medocal icons/afternoon.png" />
          <List title="Evening" url="./assests/medocal icons/evening.png" />
        </div>
        <div className={classes.timeInterval}>
            <List title="11:30" />
            <List title="12:30" />
            <List title="1:00" />
            <List title="1:45" />
            <List title="2:30" />
            <List title="3:15" />
            <List title="4:00" />
        </div>
      </div>
      <Navigation
        left={<ArrowBackIcon style={{ fontSize: "30px" }} />}
        right={<ArrowForwardIcon style={{ fontSize: "30px" }} />}
        leftClick={()=> props.history.replace('/third')}
        rightClick={()=> props.history.push('/fifth')}
      />
    </div>
  );
};

export default Fourth;
