import React from "react";
import classes from "./Third.module.css";
import Heading from "../../component/UI/Heading/Heading";
import List from "../../component/List/List";
import Navigation from "../../component/Navigation/Navigation";
import Bars from "../../component/Bars/Bars";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SearchBox from "../../component/UI/SearchBox/SearchBox";
import Image from "../../component/UI/Image/Image";
import TuneIcon from "@material-ui/icons/Tune";
import Details from "../../component/Details/Details";

const Third = (props) => {
  return (
    <div className={classes.container}>
      <Bars step={3} />
      <Heading title="Skin & Hair" />
      <div className={classes.searchBox}>
        <SearchBox />
        <div className={classes.filterIcon}>
          <TuneIcon />
        </div>
      </div>
      <div className={classes.listContainer}>
        <Details
          url="./assests/booking.png"
          name="Dr.Daisy Murphy"
          speciality="Dermatologist"
          exp={11}
          address="15780 Mckee Rd, upatoi, GA, 31829"
          buttons
          onBook={()=> props.history.push('/fourth')}
        />
        <Details
          url="./assests/booking.png"
          name="Dr.Daisy Murphy"
          speciality="Dermatologist"
          exp={11}
          address="15780 Mckee Rd, upatoi, GA, 31829"
          buttons
          onBook={()=> props.history.push('/fourth')}
        />
        <Details
          url="./assests/booking.png"
          name="Dr.Daisy Murphy"
          speciality="Dermatologist"
          exp={11}
          address="15780 Mckee Rd, upatoi, GA, 31829"
          buttons
          onBook={()=> props.history.push('/fourth')}
        />
        <Details
          url="./assests/booking.png"
          name="Dr.Daisy Murphy"
          speciality="Dermatologist"
          exp={11}
          address="15780 Mckee Rd, upatoi, GA, 31829"
          buttons
          onBook={()=> props.history.push('/fourth')}
        />
      </div>
      <Navigation left={<ArrowBackIcon style={{ fontSize: "30px" }} />} 
      leftClick={()=> props.history.replace('/second')}/>
    </div>
  );
};

export default Third;
